import React, { useState } from "react";
import "./App.css";
import { send } from "./api";

function App() {
  const [publicKey, setPublicKey] = useState('');
  const [privateKey, setPrivateKey] = useState('');
  const [title, setTitle] = useState('');
  const [body, setBody] = useState('');
  const [imageUrl, setImageUrl] = useState('');
  const [fcmTokens, setFcmTokens] = useState('');
  const [clientScope, setClientScope] = useState('');
  const [email, setEmail] = useState('');
  const [res, setRes] = useState('');
  const [error, setError] = useState('');
  const [loading, setLoading] = useState(false);

  const handleSubmit = (event) => {
    event.preventDefault();
    setError('');
    setRes('');
    setLoading(true);
    send({
      Method: 'PushNotification',
      Payload: {
        Title: title,
        Body: body,
        ImageUrl: imageUrl,
        FCMTokens: fcmTokens.split(',').filter(i => i),
        ClientScope: clientScope,
        UserField: 'Email',
        UserInfo: email.split(',').filter(i => i),
      }
    }, publicKey, privateKey)
    .then(function (response) {
      setRes(response.statusText);
    })
    .catch(function (error) {
      setError(error?.message);
    })
    .finally(() => {
      setLoading(false);
    });
  }
  return (
    <form onSubmit={handleSubmit}>
      <h1>Send Notification Demo</h1>

      <label>
        Public Key: (Used to connect with other services, like frictionless and media server)
        <input
          name="publicKey"
          type="text"
          value={publicKey}
          onChange={e => setPublicKey(e.target.value)}
          required />
      </label>

      <label>
        Private Key: (Used to connect with other services, like frictionless and media server)
        <input
          name="privateKey"
          type="text"
          value={privateKey}
          onChange={e => setPrivateKey(e.target.value)}
          required />
      </label>

      <label>
        Title:
        <input
          name="title"
          type="text"
          value={title}
          onChange={e => setTitle(e.target.value)}
          required />
      </label>
      
      <label>
        Body:
        <input
          name="body"
          type="text"
          value={body}
          onChange={e => setBody(e.target.value)} />
      </label>

      <label>
        Image Url:
        <input
          name="imageUrl"
          type="url"
          value={imageUrl}
          onChange={e => setImageUrl(e.target.value)} />
      </label>

      <label>
        FCM Tokens: (Optional, if FCM Token of a specific device is known) (list separated by comma)
        <input
          name="fcmTokens"
          type="text"
          value={fcmTokens}
          onChange={e => setFcmTokens(e.target.value)}
          placeholder="token1,token2,token3" />
      </label>

      <label>
        Client Scope: (Required if using emails below)
        <input
          name="clientScope"
          type="text"
          value={clientScope}
          onChange={e => setClientScope(e.target.value)}
          placeholder="awm, quickeats, oxxo, canteen" />
      </label>

      <label>
        Emails: (list of emails separated by comma)
        <input
          name="email"
          type="text"
          value={email}
          onChange={e => setEmail(e.target.value)}
          placeholder="one@email.com,two@email.com,three@email.com" />
      </label>

      <p>Note: there's also a "Data" field that can be sent, which can be used to send additional data to the mobile device. This is useful because it allows deep linking in the future if we have it implemented. I didn't include it for now since it doesn't do anything right now.</p>

      <button disabled={loading}>{!loading ? 'Submit' : <div className="lds-ellipsis"><div></div><div></div><div></div><div></div></div>}</button>

      <p>Result: {res}</p>
      <p>Error: <span className="error">{error}</span></p>
    </form>
  );
}

export default App;
