import axios from 'axios';
import crypto from 'crypto-js';

const HMAC = (url, querystring, method, body, date, publicKey, privateKey) => {
  const bodyHash = body ? crypto.MD5(body).toString(crypto.enc.Base64) : '';
  const data = method.toLowerCase() + url.toLowerCase() + querystring.toLowerCase() + date + publicKey + bodyHash;
  return crypto.HmacSHA256(data, privateKey).toString(crypto.enc.Base64);
}

export const send = (body, publicKey, privateKey) => {
  const date = Math.floor(Date.now() / 1000);
  const hash = HMAC(`/api/notification`, '', 'post', JSON.stringify(body), date, publicKey, privateKey);

  return axios.post('/api/notification', body, {
    baseURL: 'https://non-prod-notifications-awm.azurewebsites.net/',
    headers: {'Authorization': `${publicKey},${date},${hash}`}
  })
}